import React from 'react';

export default function AppPageLoading() {
	return (
		<div
			id="spinner" className="show bg-white position-fixed translate-middle w-100 vh-100 top-50 start-50 d-flex
			align-items-center justify-content-center" >
			<div
				className="spinner-border text-primary d-flex align-items-center justify-content-center"
				style={{ width: '5rem', height: '5rem' }}
				role="status"
			>
				<div className="spinner-border text-primary" style={{ width: '3rem', height: '3rem' }} role="status" />
			</div>
		</div>
	);
}
